.FormData{

    margin: auto;

    width: 80%;

    text-align: center;

    box-shadow: 0 2px 3px #ccc;

    border: 1px solid #eee;

    padding: 50px;

    box-sizing: border-box;
}


@media only screen and (max-width: 600px) {

    .FormData {
     width: 100%;
     margin: auto;
     padding: 0;
    }

  }


@media(min-width:600px){

    .FormData{

        width: 500px;
        
    }
